<template>
  <view class="card-list">
    <view 
      v-for="(item, index) in items"
      :key="index"
      class="card-item"
    >
      <text class="type-tag plan-tag">{{ typeMap[type] }}</text>
      <view class="card-content">
        <view class="card-header">
          <text class="card-title">{{ item.title }}</text>
          <text class="card-time">
            {{ dateRange }}
          </text>
        </view>
        <view class="card-meta">
          <text class="card-location">地点： {{ item.destination }}</text>
        </view>
        <view class="status-wrapper">
          <text :class="['status', statusClass]">{{ statusText }}</text>
        </view>
      </view>
      <button 
        class="delete-btn"
        @click.stop="$emit('delete', item)"
      >
        删除
      </button>
    </view>
  </view>
</template>

<script setup>
import { computed } from 'vue';
import { formatDate } from '@/utils/util';

const props = defineProps({
  items: {
    type: Array,
    required: true
  },
  type: {
    type: String,
    default: 'plan'
  }
});

// 状态显示逻辑
const statusMap = {
  pending: { text: '进行中', class: 'pending' },
  completed: { text: '已完成', class: 'completed' },
  cancelled: { text: '已取消', class: 'cancelled' }
};

const statusText = computed(() => (item) => 
  statusMap[item.status]?.text || item.status
);

const statusClass = computed(() => (item) => 
  statusMap[item.status]?.class || 'default'
);

// 日期格式化
const dateRange = computed(() => (item) => 
  `${formatDate(item.start_date)} - ${formatDate(item.end_date)}`
);

const typeMap = {
  plan: '旅行行程'
};
</script>

<style lang="scss" scoped>
/* 继承卡片列表原有样式 */
.status-wrapper {
  margin-top: 16rpx;
  
  .status {
    display: inline-block;
    padding: 6rpx 16rpx;
    border-radius: 20rpx;
    font-size: 24rpx;
    
    &.pending { background: #fff8e6; color: #fa8c16; }
    &.completed { background: #e6f7ff; color: #1890ff; }
    &.cancelled { background: #fff1f0; color: #f5222d; }
  }
}

.delete-btn {
  position: absolute;
  right: 32rpx;
  top: 50%;
  transform: translateY(-50%);
  background: #fff1f0;
  color: #f5222d;
  border: 1rpx solid #ffa39e;
  padding: 16rpx 32rpx;
  border-radius: 40rpx;
  font-size: 28rpx;
  line-height: 1.5;
}

.card-meta {
  .card-preference {
    color: #666;  // 保持与原列表样式一致
  }
}
</style>